@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body{
    background: #fffdff;
    color: #262727;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.videoWrapper{
    position: relative;
    overflow: hidden;     
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

#myVideo {
    object-fit: cover;
    overflow: hidden;
    height: 100vh;
    width: 100%;
}

.scrollDown{
    color: #fffdff;
    position: absolute;
    top: 0;

    font-family: 'Montserrat', sans-serif;
    font-size: 30px;

    top: 90vh;
}

.arrowDown{
    margin-left: 40%;
    display: block;
    border-top: 5px #ffffff solid;
    border-right: 5px #ffffff solid;
    width: 30px;
    height: 30px;
    rotate: 135deg;
    border-radius: 5px;
}

/* шапка */

.wrapper{
    max-width: 1200px;
    margin: 50px auto;
}

.headerSh{
    display: flex;
    width: 100%;
    justify-content: space-between;

}

.planet{
    width: 100px;
    height: 100px;
    transform: translateX(-20px);
}

header .logo{
    font-weight: 600;
    letter-spacing: 7px;
    font-family: 'Montserrat', sans-serif;
    padding-top: 50px;
}

header .presentation{
    margin: 50px 0;
    background: url(img/airflow.png);
    width: 100%;
    height: 600px;
    background-size: cover;
}

.cart{
    transition: all 0.3s;
    cursor: pointer;
    transform: scale(3);
    position: fixed;
    top: 8vh;
    right: 10vw;
    z-index: 99;
}

.openCart.false{
    left: 0;
    transition: all 0.3s;
    position: fixed;
    width: 100vw;
    height: 100vh;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.8);
    top: 5vh;
    opacity: 1;
    border-radius: 20px;
    border: 1px black solid;
    
}

.openCart.active{
    opacity: 1;
    transition: all 0.3s;
}

.smallImg{
    width: 150px;
    height: 150px;
    padding: 10px;
}

.under_header{
    width: 100%;
    text-align: center;
    margin-top: 6vh;
}

.addToOrders{
    cursor: pointer;
    transition: all 0.3s;
    font-size: 20px;
    padding-bottom: 2px;
}
.addToOrders:hover{
    transition: all 0.3s;
    font-size: 25px;
    background-color: rgb(230, 230, 230);
    color: #000000;
}

.order{
    transition: all 0.3s;
    border: solid 1px;
    display: flex;
    justify-content: space-evenly;
}

.order1{
    font-weight: 700;
    width: 250px;
    height: 120px;
    padding-top: 25px;
}

.orders{
    text-align: left;
    padding-top: 30px;
    margin-bottom: 10px;
    letter-spacing: 1px;
    opacity: 1;
    width: 100%;
    color: #000000;
    font-size: 20px;
    z-index: 10;
}

.trash{
    transform: scale(1.6);
    color: rgb(133, 133, 133);
    left: 50px;
    position: relative;
    cursor: pointer;
    transition: all 0.2S;
}

.trash:hover{
    transform: scale(1.6);
    color: red;
    left: 50px;
    top: -50px;
    position: relative;
    cursor: pointer;
}

.styledNothing{
    color: rgb(0, 0, 0);
    font-size: 25px;
    padding-top: 10px;
}

.delivery{
    font-size: 18px;
    font-weight: 600;
}
/* ================================ */

.categories{
    display: flex;
    width: 60%;
    justify-content: space-between;
    height: 30px;
}

.categoryItem{
    cursor: crosshair;
    padding-left: 3px;
    text-align: left;
    margin-right: 10px;
    font-size: 16px;
    width: 10vw;
    height: 22px;
    border: 1.5px black solid;
    transition: all 0.2s;
}

.categoryItem:hover{
    font-size: 15px;
    color: #014085;
    transition: all 0.2s;
}

.products{
    display: grid;
    justify-content: space-between;
    flex-wrap: wrap;
    text-align: center;
    column-gap: 5px;
    margin-top: 40px;
    grid-template-columns: repeat(4, 1fr);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h2{
    font-size: 22px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 700;
    padding-bottom: 6px;
}

h3{
    font-size: 16px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500; 
    padding-bottom: 10px;
}

.price{
    font-weight: 600;
    font-size: 18px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.prevPrice{
    color: rgb(143, 143, 143);
    
}

.item{
    transition: all 0.3s;
    cursor: pointer;
    padding:20px 0px 20px 0px;
    border: solid 1px #262727;
    margin-bottom: 5px;
}

.item:hover{
    transition: all 0.2s;
    border: solid 5px rgb(73, 73, 73);
}

.product_img{
    width: 85%;
    margin-bottom: 0%;
    margin-top: -5vh;
    height: 250px;
}

.priceDesc{
    margin-bottom: 30%;

}

.modalWindow{
    display: flex;
    text-align: center;
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.9);
    transition: all 0.5s;
}

.modalWindowImg{
    width: 500px;
    height: 500px;
    margin-left: 18vw;
    margin-top: 20vh;
    margin-right: 40px;
}

.modalWindowAbout{
    display: inline;
    text-align: left;
}

.modalWindowTitle{
    margin-left: 5vw;
    margin-top: 24vh;
    font-size: 25px;
    font-weight: 700;
}

.modalWindowDesc{
    margin-left: 5vw;
    width: 30vw;
    font-size: 20px;
    margin-top: 0px;
    margin-bottom: 20px;
}

.modalWindowPrice{
    font-weight: 700;
    margin-bottom: 1vh;
    margin-top: 2vh;
    margin-left: 5vw;
    font-size: 22px;
}

.modalWindowAdd{
    font-weight: 700;
    width: 30vw;
    margin-left: 5vw;
    margin-right: 5vw;
    cursor: pointer;
    transition: all 0.3s;
    font-size: 20px;
    padding-bottom: 2px;
    padding: 7px;
    padding-left: 0;
}

.modalWindowAdd:hover{
    transition: all 0.3s;
    font-size: 20px;
    background-color: black;
    color: #fffdff;
    padding-left: 10px;
}

.modalWindow .active{
    transition: all 0.3s;
}

.allModal{
    border: 1px solid black;
}

.modalWindowSubDesc{
    margin-left: 5vw;
    width: 30vw;
    font-size: 17px;
    margin-bottom: 40px;
    font-weight: 600;
}

.fullOrder.active{
    transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    opacity: 100%;
}

.fullOrder.false{
    transition: 0.3s all;
    opacity: 0;
}

.backFocus{
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    background-color: #00000065;
    left: 0;
    top: 0;
}

.footer{
    margin-left: 46%;
}

.footerImg{
    margin-top: 5vh;
    width: 100px;
    height: 100px;
    text-align: center;
}

.footerSlo{
    margin-left: -40px;
}

@media (max-width: 700px){
    header .logo{
        font-size: 12px;
    }
    .products{
        grid-template-columns: repeat(2, 1fr);
        column-gap: -10px;
    }
    .priceDesc h2{
        font-size: 17px;
    }
    h3{
        font-size: 12px;
    }
    .price{
        font-weight: 600;
        font-size: 15px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
    .product_img{
        width: 80%;
        margin-bottom: 0%;
        margin-top: -5vh;
        height: 150px;
    }
    .categoryItem{
        width: 40vw;
    }
    .categories{
        display: flex;
        width: 100%;
        height: 20px;
        padding-left: 10px;
    }
    .planet{
        width: 70px;
        height: 70px;
        transform: translate(-6px, 20px);
    }
    .logo{
        letter-spacing: 3px;
        font-size: 15px;
    }
    .categoryItem{
        font-size: 15px;
    }
    .modalWindowImg{
        width: 250px;
        height: 250px;
        margin-top: 5vh;
        margin-right: 20px;
        margin-left: 15px;
    }
    .modalWindowTitle{
        margin-top: 3vh;
        font-size: 20px;
        font-weight: 700;
        margin-left: 4vw;
        text-align: center;
    }
    .modalWindowPrice{
        font-size: 19px;
        margin-left: 4vw;
        width: 90vw;
        text-align: center;
    }
    .modalWindowDesc{
        margin-left: 4vw;
        width: 90vw;
        font-size: 15px;
        margin-top: 0px;
        margin-bottom: 20px;
        text-align: center;
    }
    .modalWindowSubDesc{
        width: 90vw;
        text-align: center;
    }
    .modalWindow{
        display: block;
    }
    .openCart.false{
        top: 0vh;
    }
    .modalWindowAdd{
        text-align: center;
        width: 90vw;
    }
    .trash{
        left: -5px;
        top: 0px;
    }
    .headerSh{
        padding-left: 10px;
        padding-right: 10px;
    }
    .footerSlo{
        margin-left: -18vw;
    }
    .footer{
        margin-left: 40%;
    }
    #myVideo{
        background-image: url(img/airflow.png);
    }
    .item{
        margin-bottom: 10px;
    }
    .categoryItem{
        font-size: 13px;
    }
    .categoryItem:hover{
        font-size: 14px;
        color: #014085;
        transition: all 0.2s;
    }
    .order1{
        padding-left: 20px;
    }
    .scrollDown{
        top: 85vh;
    }
    .footerImg{
        width: 70px;
        height: 70px;

    }
    .trash{
        position: inherit;
        cursor: pointer;
        transition: all 0.2S;
        transform: scale(1.3);
    }
    .trash:hover{
        display: none;
    }
}